	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Nena was Emilia...","date":"09 May 2004","slug":"/2004-05-09-nena-was-emilia/","tags":"Y2004"},"html":"<p><a href=\"http://www.tierschutzverein-lueneburg.de/vermittlung.php?rubrik=Katzen\" title=\"Emilia's Homepage at Tierschutzverein L&#252;neburg\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/05/emilia.jpg\" height=\"158\" width=\"320\" border=\"0\" alt=\"emilia\" /></a></p>\n<p>and had her own home page at the animal shelter in L&#252;neburg.</p>"},"next":{"fields":{"title":"IMAP for aggregators","date":"09 May 2004","slug":"/2004-05-09-imap-for-aggregators/"}},"previous":{"fields":{"title":"","date":"10 May 2004","slug":"/2004-05-10-ongoing-torture/"}}},{"node":{"fields":{"title":"IMAP for aggregators","date":"09 May 2004","slug":"/2004-05-09-imap-for-aggregators/","tags":"Y2004"},"html":"<p>I've been playing with Net News Wire, and it seems to be as good as everyone says it is. It's the first desktop news aggregator I've tried that makes me feel like I want to keep using it. But it's still just a desktop aggregator... I can't use it at work, and it only works when the machine is turned on and connected.</p>\n<p>Which got me thinking... wouldn't it be nice if news readers worked like IMAP mail clients? A server could collect the feeds and keep track of which items had been read, and I could use whatever reader I want from whatever machine I want. Just a thought...</p>"},"next":{"fields":{"title":"code: theWebSocket; - Apple Archives","date":"09 May 2004","slug":"/2004-05-09-code-thewebsocket-apple-archives/"}},"previous":{"fields":{"title":"Nena was Emilia...","date":"09 May 2004","slug":"/2004-05-09-nena-was-emilia/"}}},{"node":{"fields":{"title":"code: theWebSocket; - Apple Archives","date":"09 May 2004","slug":"/2004-05-09-code-thewebsocket-apple-archives/","tags":"Y2004"},"html":"<p><a title=\"What to install on my PowerBook, Part 2\" href=\"http://www.ahawkins.org/mt/archives/cat_apple.html\">code: theWebSocket; - Apple Archives</a></p>"},"next":{"fields":{"title":"Brad Choate: OS X software inventory","date":"09 May 2004","slug":"/2004-05-09-brad-choate-os-x-software-inventory/"}},"previous":{"fields":{"title":"IMAP for aggregators","date":"09 May 2004","slug":"/2004-05-09-imap-for-aggregators/"}}}],"pathPrefix":"","first":false,"last":false,"index":491,"pageCount":1101}}

